import { AfterViewInit, Attribute, Directive, EventEmitter, Input, Output } from '@angular/core';
import { EditorConfig } from 'src/environments/EditorConfig';
declare var editormd: any;
declare var $: any;
@Directive({
  selector: '[appEditor]'
})
export class EditorDirective implements AfterViewInit {
  @Input() editormdConfig: EditorConfig; // 
  @Output() onEditorChange: EventEmitter<string> = new EventEmitter<string>(); // 
  editor: any; // editormd

  constructor(@Attribute('id') private id: string) {
  }

  ngAfterViewInit(): void {
    this.editor = editormd(this.id, this.editormdConfig); // 

    const out = this.onEditorChange;
    const textarea = $('#' + this.id + ' :first'); // textarea

    // textareachange
    this.editor.on('change', function () {
      out.emit(textarea.val());
    });
  }

}
